import style from "./AbourActor.module.css";

function AboutActor(props) {
  const { data } = props;
  return (
    <div className={style.aboutActor}>
      <h2>': {data.name}</h2>
      <h2>
         : {data.birthday} in {data.place_of_birth}
      </h2>
      {data.deathday === null ? null : <h2> : {data.deathday}</h2>}
      <h2>: {data.gender === 1 ? "" : ""}</h2>
      <h2>: {data.popularity.toFixed(2)}</h2>
    </div>
  );
}
export default AboutActor;
